<template>
  <div class="manage container">
      <van-nav-bar fixed title="会议管理" @click-right="onClickRight">
        <van-loading slot="left"  v-if="isConnect" size="24" >获取数据中</van-loading>
        <van-icon name="plus" slot="right" />
      </van-nav-bar>
      <div class="manage-main">
        <van-tabs v-model="active" color="#3F76E0">
          <van-tab title="待参加">
            <div class="manage-card-container">
              <card
                v-for="(item,index) in toAttendList"
                :key="'to'+index"
                :date='item.date'
                :name='item.name'
                :time='item.time'
                @click="toAttendDetail"
              ></card>
            </div>
          </van-tab>
          <van-tab title="进行中">
            <div class="manage-card-container">
              <div>
                <card
                  v-for="(item,index) in attendingList"
                  :key="'to'+index"
                  :date='item.date'
                  :name='item.name'
                  :time='item.time'
                  @click="toAttendDetail"
                  ></card>
              </div>
            </div>
          </van-tab>
          <van-tab title="已发起">
            <div class="manage-card-container">
              <div>
                <card
                  v-for="(item,index) in initiateList"
                  :key="'to'+index"
                  :date='item.date'
                  :name='item.name'
                  :time='item.time'
                  @click="toAttendDetail"
                  ></card>
              </div>
            </div>
          </van-tab>
          <van-tab title="已结束">
            <div class="manage-card-container">
              <card
              v-for="(item,index) in overList"
              :key="'to'+index"
              :date='item.date'
              :name='item.name'
              :time='item.time'
              @click="toAttendDetail"
              ></card>
            </div>
          </van-tab>
        </van-tabs>
      </div>
  </div>
</template>

<script>
import { NavBar, Icon, Loading, Tab, Tabs } from 'vant'
import card from '@/components/ManageCard'
// import { getAlarmMediaPage } from '@/api/manage'
export default {
  components: {
    card,
    [NavBar.name]: NavBar,
    [Loading.name]: Loading,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Icon.name]: Icon
  },
  data () {
    return {
      isConnect: true,
      active: '',
      toAttendList: [
        {
          name: '德华的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        }
      ],
      attendingList: [
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        },
        {
          name: '朝伟的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        }
      ],
      initiateList: [
        {
          name: '胡歌的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        }
      ],
      overList: [
        {
          name: '小明的会议',
          date: '11月5日 星期二',
          time: '11/05 10:00-12:00'
        }
      ]
    }
  },
  methods: {
    onClickRight () {
      this.$router.push({
        path: '/manage/add',
        query: {}
      })
    },
    toAttendDetail () {
      this.$router.push({
        path: '/manage/detail',
        query: {}
      })
    }
  },
  created () {
    // getAlarmMediaPage()
  }
}
</script>

<style lang="postcss" scoped>
  .manage-card-container{
    height: calc(100vh - 150px);
    overflow: auto;
  }
</style>



