<template>
  <div class="mng-card border" @click="clickFn">
    <div class="mng-card-title">{{date}}</div>
    <div class="mng-card-main">
      <div>M</div>
      <div>
        <div>{{name}}</div>
        <div class="mng-card-time">{{time}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ManageCard',
  props: {
    date: {
      type: [String],
      default: ''
    },
    name: {
      type: [String],
      default: ''
    },
    time: {
      type: [String],
      default: ''
    }
  },
  methods: {
    clickFn () {
      this.$emit('click')
    }
  }
}
</script>
<style scoped>
.mng-card{
  height: 80px;
  background-color: #fff;
  border-radius: 2px;
  padding:15px;
}
.mng-card-main{
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.mng-card-main::before{
  content: '';
  width: 6px;
  height: 6px;
  border: 2px solid #ccc;
  position: absolute;
  top: 50%;
  right: 5px;
  border-left-color: transparent;
  border-bottom-color: transparent;
  transform: translateY(-5px) rotate(45deg);
}
.mng-card-main>div:first-child{
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: #01E1A9;
  border-radius: 5px;
  margin-right:10px;
  color: #fff;
}
.mng-card-title{
  font-weight: bold;
  padding-bottom:10px;
}
.mng-card-time{
  color: #999;
  font-size: 12px;
}
</style>



